:root {
  --text-color: #003b4a;
  --text-color-a: rgb(255, 255, 255);
  --contrast-color: rgb(191, 12, 240);
  --contrast-color-a: #ffff;
  --body-color: rgb(0, 59, 74);
  --font-alternate: 'Montserrat Alternates', sans-serif;
  --footer-color: #4ba39380;
  --blockquote-color: rgba(255, 215, 0, 0.8);
}

.header {
  font-weight: 600;
  background-color: rgb(241, 245, 249);
  align-content: center;

}

.contenido {
  text-align: center;
  padding: 0%;
}

.contenido li {
  display: inline-block;
  vertical-align: top;
}

.perota_menu {
  background-image: url('/src/img/perota.jpeg');
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-color: rgba(0, 0, 0, 0.9);
  color: white;
  height: 60vh;
  box-sizing: border-box;
}

.general {
  margin-left: 0.5rem;
  padding: 0.3rem;
  box-sizing: border-box;
  margin-right: 0.5rem;
  text-decoration: none;
  margin-top: 0.5rem;

}

.canciones {
  position: relative;
  display: inline-block;
  display: flex;
  justify-content: space-between;
  height:auto;
  margin-top: 1rem;
 
}
.canciones img{
  max-width: 50%;
}
.canciones h3{

  text-align: right;
  align-content: center;

}
ul,ol{
  list-style: none;
 
}
.news{
  padding: 1rem;
}
.news a{
  color: var(--blockquote-color);
}
.news button{
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
  

  
}

.canciones {
  h3 {
    text-decoration: none;
    font-size: 1.3rem;
    color: var(--contrast-color-a); 
  }
}


figure  {
  margin: 0;

}
figcaption{
  color:var(--contrast-color-a);
}
.canciones>figcaption{
  font-size: smaller;
  margin-top: 1rem;
  display: block;
  bottom: 0;
  position: absolute;
  width: 100%;

}

.portada a {
  color: var(--blockquote-color);
  margin-left: 0%;
  margin-right: 0%;
}
.portada img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

body {
  font-family: var(--font-alternate), sans-serif;
  font-weight: 400;
  color: var(--contrast-color-a);
  background-color: var(--body-color);
  width: 100%;
  height: 100%;
}

h1 {
  font-weight: 900;
  font-size: smaller;
  font-family: var(--font-alternate), sans-serif;
  width: 100%;
  text-align: center;
}

h2 {
  text-align: center;
  font-size: large;
}



.discos a {
  color: var(--blockquote-color);
}

.section1 a {
  color: var(--blockquote-color);
}

.boton {
  background-color: var(--text-color-a);
  cursor: pointer;
  border-radius: 155px;
  transition: background-color 0.3s;
  align-items: center;
  padding: 5px 5px;
}

.boton_activo {
  text-decoration: underline;
}

.Mas_info p {
  text-align: end;
}

.Mas_info a {
  color: var(--text-color);
  text-decoration: none;
}

.album {
  padding: 1rem;
}



.tour a {
  color: inherit;
  text-decoration: none;
  transition: color 0.3s;
}

.tour a:hover {
  color: var(--contrast-color);
}

.info button:hover,
.boton:hover {
  background-color: var(--contrast-color);
  color: white;
}

blockquote {
  width: 90%;
  margin: 1rem auto;
  padding-left: 0.5rem;
  border-left: 4px solid var(--blockquote-color);
  font-size: 0.9rem;
}

table {
  border-collapse: collapse;
  width: 100%;
}

th, td {
  border: 1px solid var(--contrast-color-a);
  padding: 8px;
  text-align: left;
}

th {
  background-color: var(--footer-color);
}

.slideshow-container {
  max-width: 1000px;
  position: relative;
  margin: auto;
}

.mySlides {
  &.fade {
      display: none;
  }
}

.prev, .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
}

.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

.prev:hover, .next:hover {
  background-color: rgba(0,0,0,0.8);
}

.text {
  color: #f2f2f2;
  font-size: 15px;
  padding: 8px 12px;
  position: absolute;
  bottom: 8px;
  width: 100%;
  text-align: center;
}

.numbertext {
  color: #f2f2f2;
  font-size: 12px;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}

.dot {
  cursor: pointer;
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.active, .dot:hover {
  background-color: #717171;
}

.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {
      opacity: .4;
  }
  to {
      opacity: 1;
  }
}

footer {
  background-color: var(--footer-color);
  width: 100%;
  display: flex;
  flex-direction: column;
}

footer a {
  color: white;
  text-decoration: none;
  transition: color 0.2s;
}

footer a:hover {
  color: #a4f479;
}

footer ul {

  list-style: none;
  padding: 0;
}

footer ul li {
  margin: 0;
}

.footer_width{
  text-align: center;
}

.redes ul {

  display: flex;
  justify-content: center;
}

.footer_info {

  line-height: normal;
  margin: 0;
  text-align: center;
}

@media screen and (max-width: 768px) {
  h1 {
      font-size: 1.2rem;
  }

  .album img {
      width: 100%;
  }

  .info {
  text-align: end;
  margin: 0.5rem;
  
      
  }

  footer {
      font-size: 0.8rem;
  }

  .video iframe {
      width: 100%;
      height: calc(100vw * 0.5625);
  }
}

@media screen and (min-width: 769px) {
  .video {
      position: relative;
      width: 100%;
      padding-bottom: 56.25%;
      height: 0;
      overflow: hidden;
  }

  .video iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
  }
}

@media screen and (min-width: 768px) {
  .perota_menu {
      height: 100vh;
  }
}
